@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(90deg, rgb(18, 15, 37) 1%, rgba(16,8,27,1) 30%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  
}


@font-face {
    font-family: 'Formula1Font';
    src: url('https://www.formula1.com/etc/designs/fom-website/fonts/F1Regular/Formula1-Regular.ttf')  format('truetype')
  }

.f1-nav-container {
    background-color: black;
    margin: 0;
    top: 0;
    padding: 0;
    width: 100%;
    position: fixed;
}

.f1-nav > ul{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: 0;
    height: 20px;
    padding: 30px;
}

.f1-nav > ul > li{
    list-style: none; 
}
.f1-nav > ul > li > a{
    text-decoration: none;
    padding: 30px;
    color: whitesmoke;
    font-family: 'Formula1Font', sans-serif;
    font-weight: lighter;
}

.f1-nav > ul > li> a:hover{
    background-image: url(/f1latamdemo/static/media/flag3.083cb1d5.jpg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    position: relative;
    transition: 0.3s;
    /* opacity: 0.3; */
}


.imgLogo {
    position: relative;
    display: inline-block;
    margin-top: -170px;
    z-index: 1;
}

@font-face {
    font-family: 'Formula1Font';
    src: url('https://www.formula1.com/etc/designs/fom-website/fonts/F1Regular/Formula1-Regular.ttf')  format('truetype')
  }
section {
    font-family: 'Formula1Font', sans-serif;
    font-size: small;
    font-weight: lighter;
    color: whitesmoke;
}

.footer {
    /* background: linear-gradient(0deg, rgba(110,25,25,1) 25%, rgba(40,21,21,1) 75%); */
    background: linear-gradient(0deg, rgba(110,25,25,1) 0%, rgba(40,21,21,1) 100%);
    position: fixed;
    bottom: 0;
    width: 100%;
    height: auto;
}

.social-media > ul{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: 0;
    height: 60px;
    padding: 10px;
}

.social-media > ul > li{    
    list-style: none;
    text-align: left;
    display: flex;
    align-items: center;
}
.social-media > ul > li > a{    
    text-decoration: none;
    color: whitesmoke;
}

